import React from 'react'
/* 
  1 安装：yarn add react-router-dom
  2 导入路由组件：Router / Route / Link
  3 在 pages 文件夹中创建 Home/index.js 和 CityList/index.js 两个组件(页面)
  4 使用 Route 组件配置首页和城市选择页面
*/

import { BrowserRouter as Router, Route, Link,Redirect } from 'react-router-dom'

// 导入首页和城市选择两个组件（页面）
import Index from './Index/index'
import About from './About/index'
import Other from './Other/index'

// 导入要使用的组件

function App()  {
  return (
    <Router>
      <div className="App">
        <ul className="header" >
          <li style={{width:'33%',textDecoration:'none'}}>
            <Link to="/index" style={{textDecoration:'none'}}>首页</Link>
          </li>
          <li style={{width:'33%'}}>
            <Link to="/about" style={{textDecoration:'none'}}>关于</Link>
          </li>
          <li style={{width:'33%'}}>
            <Link to="/other" style={{textDecoration:'none'}}>其他</Link>
          </li>
        </ul>
        <Route path="/"  exact render={() => <Redirect to="/index"></Redirect>} />
        <Route path="/index"  component={Index}/>
        <Route path="/about" component={About} />
        <Route path="/other" component={Other} />
      </div>
    </Router>
  )
}

export default App
